<script>
	import { createEventDispatcher } from "svelte";
	import { state, findPlaceByWoeid, getPlace } from "../store";

	const { place, places } = state;
	const dispatch = createEventDispatcher();

	const findByWoeid = async (place) => {
		await findPlaceByWoeid(place);
		dispatch("onToggleSidebar");
	};

	import { onMount } from "svelte";

	onMount(() => {
		const mybody = document.querySelector("#xxx");
		const progressBar = document.querySelector("#progressBar");

		let totalPageHeight = mybody.scrollHeight; // 1136

		mybody.onscroll = () => {
			let newProgressHeight = Math.trunc(
				(mybody.scrollTop / mybody.scrollTopMax) * 100
			);
			progressBar.style.height = `${newProgressHeight}%`;
			progressBar.style.opacity = `${newProgressHeight}%`;
		};
	});
</script>

<div id="xxx" class="body ">
	<div id="progressBar" />
	<div id="progressBarContainer" />
	<ul class="places">
		{#each $places as item}
			<li
				style="--border-color: {item.woeid === $place.woeid
					? '#616475'
					: 'transparent'}"
				on:click={() => findByWoeid(item)}
			>
				<span>{item.title}</span>
				<span class="material-icons"> keyboard_arrow_right </span>
			</li>
		{/each}
	</ul>
</div>

<style>
	.body {
		overflow: auto !important;

		overflow: -moz-scrollbars-none;
		scrollbar-width: none;
		font-family: "Courier New", Courier, monospace;
		color: #ffffff;
		/*padding: 15% 15% 5%;*/
	}

	::-webkit-scrollbar {
		width: 0;
		background: transparent;
	}

	#progressBarContainer {
		position: absolute /* fixed */;
		top: 0;
		right: 0;
		width: 10px;
		height: 100%;
		background: rgba(255, 255, 255, 0.05);
	}

	#progressBar {
		position: absolute /* fixed */;
		top: 0;
		right: 0;
		width: 10px;
		background: linear-gradient(to top, violet, red);
		height: 0;
		opacity: 1 /*  0 */;
	}

	.places {
		padding: 0;
	}

	.places span {
		font-size: 2rem;
		font-weight: 700;
		text-align: center;
	}

	.places li {
		display: flex;
		width: 367px;
		height: 64px;
		align-items: center;
		justify-content: space-between;
		padding: 0 12px;
		margin: 16px 0;
		box-sizing: border-box;
		border: 1px solid var(--border-color);
		list-style-type: none;
	}

	.places li:hover {
		border-color: #616475;
	}

	.places li span {
		font-family: Raleway;
		font-style: normal;
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;

		color: #e7e7eb;
	}

	.places .material-icons {
		font-family: "Material Icons" !important;
		color: var(--border-color);
	}

	.places li:hover .material-icons {
		color: #e7e7eb;
	}
</style>
